module.exports = "<h1 id=links>Links</h1> <blockquote> <p>Use Bootstrap-Vue&#x2019;s custom <code>b-link</code> component for generating a standard <code>&lt;a&gt;</code> link or <code>&lt;router-link&gt;</code>. <code>&lt;b-link&gt;</code> supports the <code>disabled</code> state and <code>click</code> event propagation.</p> </blockquote> <p><code>&lt;b-link&gt;</code> is the building block for most Bootstrap-Vue components that offer link functionality.</p> <pre class=hljs><span class=hljs-tag>&lt;<span class=hljs-name>div</span>&gt;</span>\n   <span class=hljs-tag>&lt;<span class=hljs-name>b-link</span> <span class=hljs-attr>href</span>=<span class=hljs-string>&quot;#foo&quot;</span>&gt;</span>Link<span class=hljs-tag>&lt;/<span class=hljs-name>b-link</span>&gt;</span>\n<span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n\n<span class=hljs-comment>&lt;!-- link-example.vue --&gt;</span>\n</pre> <h2 id=link-type>Link type</h2> <p>By specifying a value in the <code>href</code> prop, a standard link (<code>&lt;a&gt;</code>) element will be rendered. To generate a <code>&lt;router-link&gt;</code> instead, specify the route location via the <code>to</code> prop.</p> <p>Router links support various additional props. Refer to the <a href=/docs/reference/router-links>Router support</a> reference section for details.</p> <h2 id=link-disabled-state>Link disabled state</h2> <p>Disable link functionality by setting the <code>disabled</code> prop to true.</p> <pre class=hljs><span class=hljs-tag>&lt;<span class=hljs-name>div</span>&gt;</span>\n   <span class=hljs-tag>&lt;<span class=hljs-name>b-link</span> <span class=hljs-attr>href</span>=<span class=hljs-string>&quot;#foo&quot;</span> <span class=hljs-attr>disabled</span>&gt;</span>Disabled Link<span class=hljs-tag>&lt;/<span class=hljs-name>b-link</span>&gt;</span>\n<span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n\n<span class=hljs-comment>&lt;!-- link-disabled.vue --&gt;</span>\n</pre> <p>Disabling a link will set the Bootstrap V4 <code>.disabled</code> class on the link as well as handles stoping event propegation and preventing the default action from occuring.</p> <p><strong>Note:</strong> Boostrap V4 CSS currently does not style disbled links differently than non-disabled links. You can use hte following custom CSS to style disabled links:</p> <pre class=hljs><span class=hljs-selector-tag>a</span><span class=hljs-selector-class>.disabled</span> {\n  <span class=hljs-attribute>pointer-events</span>: none;\n}\n</pre> <h2 id=component-reference>Component Reference</h2> ";

